import { Component, OnInit } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { LocalStorageService } from 'angular-2-local-storage';
import { RouterModule, Routes, Router } from '@angular/router';

@Component({
  selector: 'app-update-parcel',
  templateUrl: './updateParcel.html',
  styleUrls: ['./update-parcel.component.css']
})
export class UpdateParcelComponent implements OnInit {
imeParcele:any;
vidljivost=false;
idParcele:any;
podaci:any;
uspesnoPromenjeniPodaci=false;
  constructor(private http: Http, private localStorageService: LocalStorageService, private router: Router) { }

  ngOnInit() {
       this.idParcele = this.localStorageService.get(`idParcele`);
    this.http.get(`sessions/LogovaniKorisnik`)
      .map(res => res.json())
      .subscribe(data => {
        this.podaci = data;
        this.uzmiLang();
        this.uzmiBoje();
        if (this.podaci.success === undefined) {
          this.vidljivost = true;
          this.popuniPolja();
        }

      },
      err => console.log(err),
      () => console.log('Completed'));
  }
nazad(){
  this.router.navigate(['/user']);
}
popuniPolja() {
    var i: number;
    var imanje:any;
    this.http.get(`users/prikaziPodatkeOImanju/${this.idParcele}`)
      .map(res => res.json())
      .subscribe(data => {
        imanje = data;
        this.imeParcele =imanje[0].name;
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  promeniPodatke() {
    var novo:any;
     this.http.get(`users/promeniParcelu/${this.imeParcele}/${this.idParcele}`)
      .map(res => res.json())
      .subscribe(data => {
        novo = data;
        if (novo.success) {
          this.uspesnoPromenjeniPodaci = true;
              setTimeout(() => {
                        this.uspesnoPromenjeniPodaci = false;
                    }, 3000);

        }

      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  color: any;
  newestColor: any;
  cssStyle: any;
  cssStyleH: any;
  cssStyleT: any;
  cssStyleTab: any;
  newCss = false;
  lang = "srb";
  selectedLangInOpt: any;
  selectedLang: any;
  uzmiBoje() {

    this.http.get(`users/getUserColor/${this.podaci.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.color = data;
        console.log(this.color);
        if (this.color[0].colorOne == "default")
          this.cssStyle = "";
        else {
          this.cssStyle = this.color[0].colorOne;
          this.cssStyleH = this.color[0].colorOne + "Header";
          this.cssStyleT = this.color[0].colorOne + "Tab";


        }


      },
      err => console.log(err),
      () => console.log('Completed'));

  }

uzmiLang() {
    console.log( this.lang);
    this.http.get(`users/getUserLang/${this.podaci.id}`)
    .map(res => res.json())
    .subscribe(data => {
           this.lang = data[0]["lang"];
        console.log( this.lang);
       
    },
    err => console.log(err),
    () => console.log('Completed'));

}


}



