import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Headers, Http } from '@angular/http';
import { LocalStorageService } from 'angular-2-local-storage';
import { MsgComponent } from '../msg/msg.component';
import {NgxPaginationModule} from 'ngx-pagination'; 
@Component({
  selector: 'app-sent',
  templateUrl: './sent.component.html',
  styleUrls: ['./sent.component.css']
})
export class SentComponent implements OnInit {


  poslatePoruke = [];
  selectedMessage:any;
  brojPoruka:any;
  user:any;
  constructor(private http: Http, private localStorageService: LocalStorageService, private router: Router) { }

  ngOnInit() {
    this.http.get(`sessions/LogovaniKorisnik`)
        .map(res => res.json())
        .subscribe(data => {
          this.user = data;
          this.uzmiLang();
          this.uzmiBoje();
          if (this.user.success === undefined) {
              this.getMessages();
          }

        },
        err => console.log(err),
        () => console.log('Completed'));
  }


  getMessages() {
    this.http.get('messages/getSentMessages')
      .map(res => res.json())
      .subscribe(data => {
        this.poslatePoruke = data;
      },
      err => console.log(err),
      () => console.log('Completed'));

  }
  
  goToMessage(poruka:any){
    this.selectedMessage = {id:poruka.id};
    this.router.navigate(['./msg',this.selectedMessage]);
  }

  color: any;
  newestColor: any;
  cssStyle: any;
  cssStyleH: any;
  cssStyleT: any;
  cssStyleTab: any;
  newCss = false;
  lang = "srb";
  selectedLangInOpt: any;
  selectedLang: any;
  uzmiBoje() {

    this.http.get(`users/getUserColor/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.color = data;
        console.log(this.color);
        if (this.color[0].colorOne == "default")
          this.cssStyle = "";
        else {
          this.cssStyle = this.color[0].colorOne;
          this.cssStyleH = this.color[0].colorOne + "Header";
          this.cssStyleT = this.color[0].colorOne + "Tab";


        }


      },
      err => console.log(err),
      () => console.log('Completed'));

  }

uzmiLang() {
    console.log( this.lang);
    this.http.get(`users/getUserLang/${this.user.id}`)
    .map(res => res.json())
    .subscribe(data => {
           this.lang = data[0]["lang"];
        console.log( this.lang);
       
    },
    err => console.log(err),
    () => console.log('Completed'));

}

}



