  userIsOwner() {
    this.http.get(`permisions/userIsOwner`)
      .map(res => res.json())
      .subscribe(data => {
        this.isOwner = data.owner;

        console.log(this.isOwner + " u ng on init add types");
        this.getOwners();

      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  getOwners() {
    this.http.get(`users/getOwners`)
      .map(res => res.json())
      .subscribe(data => {
        this.sviVlasnici = data;
        this.getPerms();
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  getPerms() {
    this.http.get(`permisions/dozvoleKodVlasnika`)
      .map(res => res.json())
      .subscribe(data => {
        console.log(data.success + " " + data.dozvole)
        this.perm = data;
        if (data.dozvole !== undefined && data.dozvole.length > 0) {

          this.dozvoleKodGazde = data.dozvole.filter(x => x.vrednosti.tipDodavanje == true);

          if (this.dozvoleKodGazde.length > 0 || this.isOwner) {
            this.smeDaDodaje = true;
            this.spoji();
          }

        }
        else if (this.isOwner) {
          this.smeDaDodaje = true;
          this.spoji();

        }
      },
      err => console.log(err),
      () => console.log('Completed'));

  }


  callType(value) {
    this.podTipovi = [];
    var pom: any;
    var i: number;
       if(value==undefined) return;
    this.http.get(`users/prikaziPodtip2/${value}`)
      .map(res => res.json())
      .subscribe(data => {
        pom = data;
        this.podTipovi = [];
        for (i = 0; i < pom.length; i++)
          this.podTipovi[i] = pom[i];
       if(pom!==undefined && pom.length>0) this.podtipIzabran = pom[0].id;
        //window.alert("TU SAM!");
        this.callType2(this.podtipIzabran);
      },
      err => console.log(err),
      () => console.log('Completed'));
  }


