  pomocnaZaUnos(ime, atr, crit1, crit2, opt1, opt2) {
    /* var ime2 = 'temperature';*/
    var pom: any;
    var pom1: any;
    var pom5 = [];
    var i: number;
    var ime2 = ime;
    var atr2 = atr;
    /*   window.alert("Atribut 2 jeeeeeeeeeeee"+this.atr2);*/
    this.http.get(`users/nadjiIdSpec/${this.podtipIzabran}/${atr2}`)
      .map(res => res.json())
      .subscribe(data => {
        pom1 = data;
        for (i = 0; i < pom1.length; i++)
          pom5[i] = pom1[i];
        this.podt1 = pom5[0].id;
        /*  window.alert("Podaci su: "+this.atr2+" iii "+this.podt1+" iiiiiii "+ime2);*/
        /*    window.alert("ovde sam za idSpec temp "+this.podt1);*/
        /*   window.alert("OVDE SAM "+this.atr2);*/
        this.http.get(`users/sacuvajPodatkeZaPraviloZemljiste/${crit1}/${crit2}/${opt1}/${opt2}/${this.podtipIzabran}/${atr2}/${this.podt1}/${ime2}`)
          .map(res => res.json())
          .subscribe(data => {
            pom = data;
            this.uspesnoUneti = true;
            setTimeout(() => {
              this.uspesnoUneti = false;
            }, 3000);
            /*  if (pom.success = true)*/
            /*   window.alert("Ubacio sam!");*/
          },
          err => console.log(err),
          () => console.log('Completed'));
      },
      err => console.log(err),
      () => console.log('Completed'));

  }

