  uzmiBoje() {

    this.http.get(`users/getUserColor/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.color = data;
        console.log(this.color);
        if (this.color[0].colorOne == "default")
          this.cssStyle = "";
        else {
          this.cssStyle = this.color[0].colorOne;
          this.cssStyleH = this.color[0].colorOne + "Header";
          this.cssStyleT = this.color[0].colorOne + "Tab";
          this.cssStyleC = this.color[0].colorOne + "Check";
          console.log(this.cssStyle);


        }
        if (this.cssStyle == "green")
          $(".noUi-connect").css('background-color', '#2CC185');
        else
          $(".noUi-connect").css('background-color', '#8f2846');

      },
      err => console.log(err),
      () => console.log('Completed'));

  }
