import { Component, Input, OnInit, NgModule } from '@angular/core';
import { Plantation, Vrsta, Proizvodjac } from '../AddPlantation/plantation';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms'
import 'rxjs/add/operator/finally';
import { Observable } from 'rxjs/Observable';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import { BrowserModule } from '@angular/platform-browser'
import { MeniComponent } from "../meni/meni.component";
import { Tabs } from './tabs/tabs';
import { Tab } from './tabs/tab';
import { PermService } from "../services/perm-service.service";
import { PlantationService } from '../AddPlantation/plantation.service';
import { StatsWidgets } from '../statsWidgets/stats-widgets';
import { NgxPaginationModule } from 'ngx-pagination';
import { UniquePipe } from '../unique.pipe';
@Component({
    selector: `types-form`,
    templateUrl: './addTypes.html'
})
/*
@NgModule({
    imports:[ BrowserModule ],
    declarations:[AddTypes, Tabs, Tab],
    bootstrap:[ AddTypes ]
})
*/
export class AddTypes implements OnInit {
    constructor(private permService: PermService, private fb: FormBuilder, private plantationService: PlantationService, private http: Http) {
        this.createProducerForm();
        this.createSubtypeForm();
        this.createTypeForm();
    }
    unesiProizvodjaca = false;
    unesiKulturu = false;
    unesiPodkulturu = false;
    typeForm: FormGroup;
    producerForm: FormGroup;
    filter: boolean = false;
    subtypeForm: FormGroup;
    dozvole: any;
    tipZaBrisanje: any;
    podtipZaBrisanje: any;
    proizvodjacZaBrisanje: any;
    smeDaDodaje: boolean = false;
    dodatProizvodjac: boolean = false;
    dodatTip: boolean = false;
    dodatPodtip: boolean = false;
    tipovi = [];
    podtipovi = [];
    proizvodjaci = [];
    user: any;
    test: any;
    stats: any;
    isOwner: boolean;
    komeMozeDaDoda: any = [];
    perm = [];
    dozvoleKodGazde = [];
    idVlasnika = [];
    proizvodjacVidljiv = true;
    dodajKulturuVidljiv = false;
    dodajPodkulturuVidljiv = false;
    spisakKulturaVidljiv = false;
    spisakPodkulturuVidljiv = false;
    spisakProizvodjacaVidljiv = false;
    sviVlasnici = [];
    color: any;
    newestColor: any;
    cssStyle: any;
    cssStyleH: any;
    cssStyleT: any;
    cssStyleTab: any;
    newCss = false;
    lang = "srb";
    selectedLangInOpt: any;
    selectedLang: any;
    promeniTab() {
        this.proizvodjacVidljiv = true;
        this.dodajKulturuVidljiv = false;
        this.dodajPodkulturuVidljiv = false;
        this.spisakKulturaVidljiv = false;
        this.spisakPodkulturuVidljiv = false;
        this.spisakProizvodjacaVidljiv = false;
        this.filter = false;
    }
    promeniTab2() {
        this.proizvodjacVidljiv = false;
        this.dodajKulturuVidljiv = true;
        this.dodajPodkulturuVidljiv = false;
        this.spisakKulturaVidljiv = false;
        this.spisakPodkulturuVidljiv = false;
        this.spisakProizvodjacaVidljiv = false;
         this.filter = false;
    }
    promeniTab3() {
        this.proizvodjacVidljiv = false;
        this.dodajKulturuVidljiv = false;
        this.dodajPodkulturuVidljiv = true;
        this.spisakKulturaVidljiv = false;
        this.spisakPodkulturuVidljiv = false;
        this.spisakProizvodjacaVidljiv = false;
         this.filter = false;
    }
    promeniTab4() {
        this.proizvodjacVidljiv = false;
        this.dodajKulturuVidljiv = false;
        this.dodajPodkulturuVidljiv = false;
        this.spisakKulturaVidljiv = true;
        this.spisakPodkulturuVidljiv = false;
        this.spisakProizvodjacaVidljiv = false;
        this.filter = false;
    }
    promeniTab5() {
        this.proizvodjacVidljiv = false;
        this.dodajKulturuVidljiv = false;
        this.dodajPodkulturuVidljiv = false;
        this.spisakKulturaVidljiv = false;
        this.spisakPodkulturuVidljiv = true;
      
    }
    promeniTab6() {
        this.proizvodjacVidljiv = false;
        this.dodajKulturuVidljiv = false;
        this.dodajPodkulturuVidljiv = false;
        this.spisakKulturaVidljiv = false;
        this.spisakPodkulturuVidljiv = false;
        this.spisakProizvodjacaVidljiv = true;
        this.filter = false;
    }
    ngOnInit() {
        this.permService.getPerms().subscribe(data => {
            this.dozvole = data;
        },
            err => console.log(err),
            () => console.log('Completed'));
        let self = this;

        self.http.get(`sessions/LogovaniKorisnik`)
            .map(res => res.json())
            .subscribe(data => {
                this.user = data;
                this.uzmiBoje();
                this.uzmiLang();
                console.log(this.user);
                if (this.user.id) {

                    this.userIsOwner();

                }
                this.preuzmiStatistiku();

            },
            err => console.log(err),
            () => console.log('Completed'));
    }


    preuzmiStatistiku() {
        this.http.get(`stats/stats`)
            .map(res => res.json())
            .subscribe(data => {
                this.stats = data;


            },
            err => console.log(err),
            () => console.log('Completed'));


    }
    ObrisiTip() {
        this.http.get(`types/obrisi/${this.tipZaBrisanje}`)
            .map(res => res.json())
            .subscribe(data => {
                this.stats = data;
                this.spoji();


            },
            err => console.log(err),
            () => console.log('Completed'));
    }
    ObrisiPodtip() {
        this.http.get(`types/obrisiPodtip/${this.podtipZaBrisanje}`)
            .map(res => res.json())
            .subscribe(data => {
                this.stats = data;
                this.spoji();
            },
            err => console.log(err),
            () => console.log('Completed'));
    }
    ObrisiProizvodjaca() {
        this.http.get(`types/obrisiProizvodjaca/${this.proizvodjacZaBrisanje}`)
            .map(res => res.json())
            .subscribe(data => {
                this.stats = data;
                this.spoji();
            },
            err => console.log(err),
            () => console.log('Completed'));
    }
    postaviIzabranTip(value) {
        this.tipZaBrisanje = value;
    }
    postaviIzabranPodtip(value) {
        this.podtipZaBrisanje = value;
    }
    postaviIzabranogProizvodjaca(value) {
        this.proizvodjacZaBrisanje = value;
    }
    primeniFilter() {
        this.filter = !this.filter;
    }

    createTypeForm() {
        this.typeForm = this.fb.group({
            typeName: ['', Validators.required],
            vlasnikPlantaze: ['']

        });
    };
    createProducerForm() {
        this.producerForm = this.fb.group({
            producerName: ['', Validators.required],
            vlasnikPlantaze: ['']
        });
    };
    createSubtypeForm() {

        this.subtypeForm = this.fb.group({
            vrsta: ['', Validators.required],
            subtypeName: ['', Validators.required],
            vlasnikPlantaze: ['']
        })

    }

    addType() {
        this.unesiKulturu = false;
        const novaKultura: any = this.typeForm.value;
        var idOw;
        var vidljiv = 0;





        idOw = novaKultura.vlasnikPlantaze;
        console.log(idOw + " add types kad nije vlasnik");

        if ((novaKultura.vlasnikPlantaze == '' || novaKultura.vlasnikPlantaze == null) || (novaKultura.typeName == '' || novaKultura.typeName == null)) {
            this.unesiKulturu = true;
            setTimeout(() => {
                this.unesiKulturu = false;
            }, 3000);

        }
        else {
            console.log("vidljiv: " + vidljiv + " od user-a " + this.user.id);
            this.http.get(`types/addType/${novaKultura.typeName}/${vidljiv}/${idOw}`)
                .map(res => res.json())
                .subscribe(data => {
                    this.test = data;
                    if (this.test.success) {
                        this.dodatTip = true;
                        setTimeout(() => {
                            this.dodatTip = false;
                        }, 3000);

                        this.spoji();
                    }
                    else {
                        this.dodatTip = false;
                    }
                },
                err => console.log(err),
                () => console.log('Completed'));
        }
        this.typeForm.reset();
        console.log(novaKultura.typeName);

    }

    addSubtype() {
        this.unesiPodkulturu = false;
        const novaPodKultura = this.subtypeForm.value;
        console.log(novaPodKultura);
        var idOw = novaPodKultura.vlasnikPlantaze;
        console.log(idOw + " add types kad nije vlasnik");

        if ((novaPodKultura.vlasnikPlantaze == '' || novaPodKultura.vlasnikPlantaze == null) || (novaPodKultura.subtypeName == '' || novaPodKultura.subtypeName == null) || (novaPodKultura.vrsta == '' || novaPodKultura.vrsta == null)) {
            this.unesiPodkulturu = true;
            setTimeout(() => {
                this.unesiPodkulturu = false;
            }, 3000);
        } else {
            this.http.get(`types/addSubtype/${novaPodKultura.vrsta}/${novaPodKultura.subtypeName}/${idOw}`)
                .map(res => res.json())
                .subscribe(data => {
                    this.test = data;
                    if (this.test.success) {
                        this.dodatPodtip = true;
                        setTimeout(() => {
                            this.dodatPodtip = false;
                        }, 3000);

                        this.spoji();
                    }
                    else {
                        this.dodatPodtip = false;
                    }
                }, err => console.log(err),
                () => console.log('Completed'));
            this.subtypeForm.reset();
            console.log(novaPodKultura.typeName);
        }
    }
    addProducer() {
        this.unesiProizvodjaca = false;
        const noviProizvodjac: any = this.producerForm.value;
        var idOw = noviProizvodjac.vlasnikPlantaze;
        console.log(idOw + " add producer kad nije vlasnik");

        if ((noviProizvodjac.vlasnikPlantaze == '' || noviProizvodjac.vlasnikPlantaze == null) || (noviProizvodjac.producerName == '' || noviProizvodjac.producerName == null)) {
            this.unesiProizvodjaca = true;
            setTimeout(() => {
                this.unesiProizvodjaca = false;
            }, 3000);

        } else {
            this.http.get(`types/addProducer/${noviProizvodjac.producerName}/${idOw}`)
                .map(res => res.json())
                .subscribe(data => {
                    this.test = data
                    if (this.test.success) {

                        this.dodatProizvodjac = true;
                        setTimeout(() => {
                            this.dodatProizvodjac = false;
                        }, 3000);
                        var novPr: Proizvodjac = ({
                            id: this.test.id,
                            name: noviProizvodjac.producerName
                        });
                        this.spoji();
                    }
                    else {
                        this.dodatProizvodjac = false;
                    }

                },
                err => console.log(err),
                () => console.log('Completed'));
            this.producerForm.reset();
            console.log(noviProizvodjac.producerName);
        }
    }
    getTypes(id): Vrsta[] {
        console.log(this.user.id + " get tajps");
        this.http.get(`types/getTypes/${id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.tipovi = data as Vrsta[];
                for (var i = 0; i < this.tipovi.length; i++) {
                    var dozvola = this.dozvoleKodGazde.find(x => x.vlasnik == this.tipovi[i].idUser);
                    if ((dozvola !== undefined && dozvola !== null) || (this.user.id == this.tipovi[i].idUser)) this.tipovi[i].smeDaBrise = true;
                    else this.tipovi[i].smeDaBrise = false;
                }
            },
            err => console.log(err),
            () => console.log('Completed'));

        return this.tipovi;
    }

    getSubtypesById(id): any {
        this.http.get(`types/getSubtypes/${id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.podtipovi = data;
                for (var i = 0; i < this.podtipovi.length; i++) {
                    var dozvola = this.dozvoleKodGazde.find(x => x.vlasnik == this.podtipovi[i].idUser);
                    if ((dozvola !== undefined && dozvola !== null) || (this.user.id == this.podtipovi[i].idUser)) this.podtipovi[i].smeDaBrise = true;
                    else this.podtipovi[i].smeDaBrise = false;
                }
            },
            err => console.log(err),
            () => console.log('Completed'));


        console.log(" pa podtipovi" + this.podtipovi);
        return this.podtipovi;
    }
    getProducers(id): any[] {
        this.http.get(`types/getProducers/${id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.proizvodjaci = data;
                for (var i = 0; i < this.proizvodjaci.length; i++) {
                    var dozvola = this.dozvoleKodGazde.find(x => x.vlasnik == this.proizvodjaci[i].idUser);
                    if ((dozvola !== undefined && dozvola !== null) || (this.user.id == this.proizvodjaci[i].idUser)) this.proizvodjaci[i].smeDaBrise = true;
                    else this.proizvodjaci[i].smeDaBrise = false;
                }
            },
            err => console.log(err),
            () => console.log('Completed'));


        return this.proizvodjaci;
    }

    userIsOwner() {
        this.http.get(`permisions/userIsOwner`)
            .map(res => res.json())
            .subscribe(data => {
                this.isOwner = data.owner;

                console.log(this.isOwner + " u ng on init add types");
                this.getOwners();

            },
            err => console.log(err),
            () => console.log('Completed'));
    }

    getOwners() {
        this.http.get(`users/getOwners`)
            .map(res => res.json())
            .subscribe(data => {
                this.sviVlasnici = data;
                this.getPerms();
            },
            err => console.log(err),
            () => console.log('Completed'));
    }

    getPerms() {
        this.http.get(`permisions/dozvoleKodVlasnika`)
            .map(res => res.json())
            .subscribe(data => {
                console.log(data.success + " " + data.dozvole)
                this.perm = data;
                if (data.dozvole !== undefined && data.dozvole.length > 0) {

                    this.dozvoleKodGazde = data.dozvole.filter(x => x.vrednosti.tipDodavanje == true);

                    if (this.dozvoleKodGazde.length > 0 || this.isOwner) {
                        this.smeDaDodaje = true;
                        this.spoji();
                    }

                }
                else if (this.isOwner) {
                    this.smeDaDodaje = true;
                    this.spoji();
                }
            },
            err => console.log(err),
            () => console.log('Completed'));

    }
    spoji() {
        console.log("SPOJI");
        this.komeMozeDaDoda = [];
        this.idVlasnika = [];
        for (var i = 0; this.sviVlasnici !== undefined && i < this.sviVlasnici.length; i++)
            for (var j = 0; this.dozvoleKodGazde !== undefined && j < this.dozvoleKodGazde.length; j++) {
                console.log(this.sviVlasnici[i].idOwner + " " + this.dozvoleKodGazde[j].vlasnik);
                if (this.sviVlasnici[i].idOwner == this.dozvoleKodGazde[j].vlasnik) {
                    console.log(this.sviVlasnici[i])
                    // var noviID = {id:this.sviVlasnici[i].idOwner};
                    var noviG = {
                        id: this.sviVlasnici[i].idOwner,
                        name: this.sviVlasnici[i].name,
                        surname: this.sviVlasnici[i].surname,
                        username: this.sviVlasnici[i].username,
                        email: this.sviVlasnici[i].email
                    };
                    this.komeMozeDaDoda.push(noviG);
                    this.idVlasnika.push(this.sviVlasnici[i].idOwner);
                    console.log(this.komeMozeDaDoda[0]);
                }
            }
        if (this.isOwner) {
            var noviG = {
                id: this.user.id,
                name: this.user.name,
                surname: this.user.surname,
                username: this.user.username,
                email: this.user.email
            };
            this.komeMozeDaDoda.unshift(noviG);
            this.idVlasnika.unshift(this.user.id);
        }
        this.tipovi = this.getTypes(this.idVlasnika);
        this.podtipovi = this.getSubtypesById(this.idVlasnika);
        this.proizvodjaci = this.getProducers(this.idVlasnika);
    }
    uzmiBoje() {

        this.http.get(`users/getUserColor/${this.user.id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.color = data;
                console.log(this.color);
                if (this.color[0].colorOne == "default")
                    this.cssStyle = "";
                else {
                    this.cssStyle = this.color[0].colorOne;
                    this.cssStyleH = this.color[0].colorOne + "Header";
                    this.cssStyleT = this.color[0].colorOne + "Tab";


                }


            },
            err => console.log(err),
            () => console.log('Completed'));

    }

    uzmiLang() {
        console.log(this.lang);
        this.http.get(`users/getUserLang/${this.user.id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.lang = data[0]["lang"];
                console.log(this.lang);

            },
            err => console.log(err),
            () => console.log('Completed'));

    }
}


