import { Component, OnInit } from '@angular/core';
import { Headers, Http } from '@angular/http';
@Component({
  selector: 'app-new-msg',
  templateUrl: './new-msg.component.html',
  styleUrls: ['./new-msg.component.css']
})
export class NewMsgComponent implements OnInit {

  constructor(private http: Http) { }
  porukaPoslata: boolean;
  username: any = '';
  poruka: any = '';
  filteredList = [];
  useri = [];
  ngOnInit() {
    this.http.get(`sessions/LogovaniKorisnik`)
      .map(res => res.json())
      .subscribe(data => {
        this.user = data;
        this.uzmiBoje();
        this.uzmiLang();
        console.log(this.user);
        if (this.user.id) {
        }

      },
      err => console.log(err),
      () => console.log('Completed'));
    this.http.get(`users/getUsers`)
      .map(res => res.json())
      .subscribe(data => {
       var pom: any;
        var i: number;
        pom = data;
        for (i = 0; i < pom.length; i++)
          this.useri[i] = pom[i].username;

      },
      err => console.log(err),
      () => console.log('Completed'));
  }
  sendMessage() {
    this.http.get(`messages/sendMessage/${this.username}/${this.poruka}`)
      .map(res => res.json())
      .subscribe(data => {
        if (data.success) {
          this.porukaPoslata = true;
          this.username = '';
          this.poruka = '';
        }
        else
          this.porukaPoslata = false;
      },
      err => console.log(err),
      () => console.log('Completed'));

  }
  user: any;
  color: any;
  newestColor: any;
  cssStyle: any;
  cssStyleH: any;
  cssStyleT: any;
  cssStyleTab: any;
  newCss = false;
  lang = "srb";
  selectedLangInOpt: any;
  selectedLang: any;
  uzmiBoje() {

    this.http.get(`users/getUserColor/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.color = data;
        console.log(this.color);
        if (this.color[0].colorOne == "default")
          this.cssStyle = "";
        else {
          this.cssStyle = this.color[0].colorOne;
          this.cssStyleH = this.color[0].colorOne + "Header";
          this.cssStyleT = this.color[0].colorOne + "Tab";


        }


      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  uzmiLang() {
    console.log(this.lang);
    this.http.get(`users/getUserLang/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.lang = data[0]["lang"];
        console.log(this.lang);

      },
      err => console.log(err),
      () => console.log('Completed'));

  }
  filter1() {
    console.log("tu smo!");
    if (this.username !== "") {
      console.log("e sad smo tu smo!");
      this.filteredList = this.useri.filter(function (el) {
        return el.toLowerCase().indexOf(this.username.toLowerCase()) > -1;
      }.bind(this));
    } else {
      this.filteredList = [];
    }
  }

  select(item) {
    this.username = item;
    this.filteredList = [];
  }

}



