import { Component } from '@angular/core';

import { Headers, Http } from '@angular/http';
import { MeniComponent } from "../meni/meni.component";
import { PermService } from "../services/perm-service.service";
import { FilterPipe } from '../filter.pipe';
import { StatsWidgets } from '../statsWidgets/stats-widgets';


@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.css']
})
export class FaqComponent{
    ime: any;
    naslov: any;
    email: any;
    poruka: any;
    poslato=false;
    pom:any;
    pitanjaOdgovori=[];
    user:any;
    dozvole = "any";
      lang = "srb";
    selectedLangInOpt: any;
    selectedLang: any;
    constructor(private permService: PermService, private http: Http) {

    }

  ngOnInit() {
      this.http.get(`sessions/LogovaniKorisnik`)
        .map(res => res.json())
        .subscribe(data => {
          this.user = data;  
           this.uzmiLang();
          this.ime=this.user.name;
          this.email=this.user.email;        
        },
        err => console.log(err),
        () => console.log('Completed'));
        this.ucitajPitanjaOdgovore();

  }
   posalji() {
    var pom3: any;
    this.http.get(`users/posaljiMail2/${this.ime}/${this.naslov}/${this.email}/${this.poruka}`)
      .map(res => res.json())
      .subscribe(data => {
        pom3 = data;
        if (pom3.success) {
            this.poslato=true;
             setTimeout(() => {
                        this.poslato = false;
                    }, 3000);
            this.ime="";
             this.naslov="";
              this.email="";
              this.poruka="";
        }
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

ucitajPitanjaOdgovore()
{
  var i:number;
  this.http.get(`users/vratiFAQ`)
                .map(res => res.json())
                .subscribe(data => {
                    this.pom = data
                    for(i=0;i<this.pom.length;i++)
                      this.pitanjaOdgovori[i]=this.pom[i];
                },
                err => console.log(err),
                () => console.log('Completed reg'));
}
   uzmiLang() {
        console.log(this.lang);
        this.http.get(`users/getUserLang/${this.user.id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.lang = data[0]["lang"];
                console.log(this.lang);

            },
            err => console.log(err),
            () => console.log('Completed'));

    }

}



