import { Component, OnInit } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { RouterModule, Routes, Router } from '@angular/router';
import { LocalStorageService } from 'angular-2-local-storage';

@Component({
  moduleId: module.id,
  selector: 'app-widgets',
  templateUrl: './index.html'


})

export class StatsWidgets implements OnInit {

    constructor(private http: Http, private router: Router, private localStorageService: LocalStorageService) {

  }
  vidljiv=true;
  widget="hideWidget";
  stats: any;
   lang="srb";
  selectedLangInOpt:any;
  selectedLang:any;
  user:any;  prvihPet: any = [];
  prvihPetExp:any = [];
  prvihPetPoruka: any = [];
  poslatePoruke: any = [];
  primljenePoruke: any = [];
  porukaPoslata: boolean = false;
  brojPoruka: any;
  obavestenjaVidljiva: boolean = false;
  obavestenjaStigla: boolean = false;
  tip: string[];
  obavestenja: any = [];
  expObavestenja:any = []; ukupnoObavestenja = 0;
   meniMultiLang= {
"srb": [
/*0*/       {  "registrovanih plantaza":" registrovanih plantaa" },
/*1*/       {"registrovane plantaze":" registrovane plantae"},
/*2*/       { "nemate registrovanih plantaza":" nemate registrovanih plantaa"},
/*3*/       { "registrovan radnik":"registrovan radnik"},
/*4*/       { "registrovana radnika":"registrovana radnika"},
/*5*/       { "registrovanih radnika":"registrovanih radnika"},
/*6*/       { "nemate registrovanih radnika":"nemate registrovanih radnika"},
/*7*/       {"registrovan ekspert":"registrovan ekspert"},
/*8*/       { "registrovana eksperta":"registrovana eksperta"},
/*9*/       { "registrovanih eksperata":"registrovanih eksperata"},
/*10*/      { "nemate registrovanih eksperata":"nemate registrovanih eksperata"},
/*11*/      {"obavestenje":"obavetenje"},
/*12*/      {"obavestenja":"obavetenja"},
/*13*/      { "nemate obavestenja":"nemate obavetenja"},

    ],
  "eng": [
    /*0*/   {  "registrovanih plantaza":" registered plantation" },
/*1*/       {"registrovane plantaze":"registered plantations"},
/*2*/       { "nemate registrovanih plantaza":"you do not have registered plantation"},
/*3*/       { "registrovan radnik":"registered worker"},
/*4*/       { "registrovana radnika":"registered workers"},
/*5*/       { "registrovanih radnika":"registered workers"},
/*6*/       { "nemate registrovanih radnika":"you do not have registered workers"},
/*7*/       {"registrovan ekspert":"registered expert"},
/*8*/       { "registrovana eksperta":"registered experts"},
/*9*/       { "registrovanih eksperata":"registered experts"},
/*10*/      { "nemate registrovanih eksperata":"you do not have any registered experts"},
/*11*/      {"obavestenje":"Notification"},
/*12*/      {"obavestenja":"Notifications"},
/*13*/      { "nemate obavestenja":"you do not have any notification"},
    ],
 

   };
skarijMeni()
{
    
    this.vidljiv=!this.vidljiv;
    this.localStorageService.set("meniS",  this.vidljiv);
    if(!this.vidljiv)
    {
          this.widget="showWidget";
    }
    else{
         this.widget="hideWidget";
    }
    
}
    ngOnInit() {
        var i;
       i=this.localStorageService.get("meniS");
       this.vidljiv=i;
        if(!this.vidljiv)
        {
            this.widget="showWidget";
        }
        else{
            this.widget="hideWidget";
        }
        this.preuzmiStatistiku();
        this.selectedLang=this.meniMultiLang[this.lang];
        this.uzmiObavestenja();
        this.uzmiExpObavestenja();

    
      this.http.get(`sessions/LogovaniKorisnik`)
        .map(res => res.json())
        .subscribe(data => {
          this.user = data;
 
            this.uzmiLang();
      

        },
        err => console.log(err),
        () => console.log('Completed'));

    }

    preuzmiStatistiku() {

    this.http.get(`stats/stats`)
      .map(res => res.json())
      .subscribe(data => {
        this.stats = data;
        console.log(data);

      },
      err => console.log(err),
      () => console.log('Completed'));


  }
  uzmiObavestenja() {
    this.prvihPet = [];
    this.obavestenja = [];
    this.http.get(`users/dohvatiObavestenja`)
      .map(res => res.json())
      .subscribe(data => {
        this.obavestenja = data;
        if (this.obavestenja !== undefined) {
          console.log("stiglo");
          for (var i = 0; i < 3; i++)
            if (this.obavestenja[i] !== undefined) this.prvihPet.push(this.obavestenja[i]);
            else break;
          this.obavestenjaStigla = true;
          this.ukupnoObavestenja = this.obavestenja.length;
        }

      },
      err => console.log(err),
      () => console.log('Completed'));

  }
  uzmiExpObavestenja(){
    this.prvihPetExp = [];
    this.expObavestenja = [];
    this.http.get(`users/expNotifications`)
      .map(res => res.json())
      .subscribe(data => {
        if(data.success){
          this.expObavestenja = data.not;
          if (this.expObavestenja.length > 0){
            for (var i = 0; i < 3-this.obavestenja.length; i++)
              if (this.expObavestenja[i] !== undefined) 
                this.prvihPetExp.push(this.expObavestenja[i]);
              else 
                break;
          this.obavestenjaStigla = true;
          this.ukupnoObavestenja = this.expObavestenja.length+this.obavestenja.length;
          console.log(this.expObavestenja)
          }
        }else{
          this.ukupnoObavestenja = this.obavestenja.length;
        }

      },
      err => console.log(err),
      () => console.log('Completed'));
  }
  promeniStranu(br){
    if(br==1)
        this.router.navigate(['./user']);
    else if(br==2)
        this.router.navigate(['./addworker'])
    else if(br==3)
        this.router.navigate(['./addworker']);
    else if(br==4)
        this.router.navigate(['./notifications']);
  }
    uzmiLang() {
 
    this.http.get(`users/getUserLang/${this.user.id}`)
    .map(res => res.json())
    .subscribe(data => {
      this.lang = data;
           this.selectedLang=this.meniMultiLang[this.lang[0]["lang"]];
       console.log(this.selectedLang);
 
    
    },
    err => console.log(err),
    () => console.log('Completed'));

}
}


