import { Component, Input, OnInit } from '@angular/core';
import { Plantation, Vrsta, Proizvodjac } from './plantation';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Headers, Http } from '@angular/http';
import { LocalStorageService } from 'angular-2-local-storage';
import 'rxjs/add/operator/map';
import { RouterModule, Routes, Router } from '@angular/router';
import { PermService } from "../services/perm-service.service";
import { UniquePipe } from '../unique.pipe';
declare var google: any;

@Component({
  selector: 'app-update-plantation',
  templateUrl: './updatePlant.html',
  styleUrls: ['./update-plantation.component.css']
})
export class UpdatePlantation implements OnInit {
  vidljivost = false;
  idPlant: any;
  vrste: any;
  dozvole: any;
  user: any;
  imePlant: any;
  imePodT: any;
  imePS: any;
  uspesnoPromenjeniPodaci = false;

  nazivTip: any;
  idTip: any;
  idT: number;

  mapProp = null;
  map = null;
  drawingManager = null;

  @Input() plantation: Plantation;
  @Input() Tip: Vrsta;
  @Input() proizvodjac: Proizvodjac;

  isLoading = false;
  ddVisible = false;
  tipovi = [];
  podtipovi = [];
  proizvodjaci = [];
  nizOverley = [];
  plantForm: FormGroup;
  nizPoligona = []; //!!!
  isOwner: boolean;
  podaciOPlant: any;
  imePlantaze;
  sviPodtipovi = [];
  sviVlasnici = [];
  perm = [];
  dozvoleKodGazde = [];
  smeDaPise = false;
  komeMozeDaDoda: any = [];
  idVlasnika = [];
nemaPoligona = false;
imePrazno=false;
  typeId;
  subtypeId;
  producerId;

  polygons = [];
  novPoligon = [];

  constructor(private permService: PermService, private fb: FormBuilder, private http: Http, private localStorageService: LocalStorageService, private router: Router) {
    this.createForm();
  }



  getRandomColor() {
    var letters = '0123456789ABCDEF';
    var color = '#';
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }
  ubaciUBounds(niz) {

    var bounds = new google.maps.LatLngBounds();
    for (var j = 0; j < niz.length; j++)
      for (var i = 0; i < niz[j].length; i++) {

        var marker = new google.maps.LatLng(niz[j][i][1], niz[j][i][0]);
        bounds.extend(marker);
      }
    return bounds;
  }

  obrisiPol = false;
  obrisiPoligone() {

    let self = this;
    for (var i = 0; i < this.polygons.length; i++) {
      this.polygons[i].setMap(null);
    }
    for (var i = 0; i < this.nizOverley.length; i++) {
      this.nizOverley[i].setMap(null);
    }
    ///   this.obrisiPol = true;
    this.nizOverley = [];
    this.novPoligon = [];
    this.nizPoligona = [];
    this.polygons = [];
    console.log(this.nizPoligona);
    console.log(this.nizOverley);
    this
    google.maps.event.trigger(self.map, 'resize');
  }


  initMap(coords) {
    let self = this;
    var poligon = [];
    var zoom = 2;
    var centar;
    var povrsina = 0;
    this.nizPoligona = []; //!!!!!!!!!
    document.getElementById('mapID').innerHTML = "<div id='googleMap' style='width: 100%; height: 700px'></div>";
    // console.log("coords[0].coordinates.length: " + coords[0].coordinates.length);
    for (var i = 0; i < coords[0].coordinates.length; i++) {
      for (var j = 0; j < coords[0].coordinates[i].length; j++) {
        poligon[poligon.length] = new google.maps.LatLng(coords[0].coordinates[i][j][1], coords[0].coordinates[i][j][0]);
      }
      this.nizPoligona.push(poligon);
      poligon = [];
    }

    var bounds = this.ubaciUBounds(coords[0].coordinates);

    self.map = new google.maps.Map(document.getElementById('googleMap'), {
      mapTypeId: 'hybrid',
      drawingControl: true,
    });
    self.drawingManager = new google.maps.drawing.DrawingManager({
      drawingControl: true,
      drawingControlOptions: {
        position: google.maps.ControlPosition.TOP_CENTER,
        drawingModes: ['polygon'],
      },
      polygonOptions: {
        editable: true,
        strokeColor: '#FFFFFF',
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: this.getRandomColor(),
        fillOpacity: 0.35
      }
    });
    self.drawingManager.setMap(self.map);
    self.map.fitBounds(bounds);

    for (var i = 0; i < this.nizPoligona.length; i++) {
      this.polygons.push(new google.maps.Polygon({
        editable: true,
        paths: this.nizPoligona[i],
        strokeColor: '#FFFFFF',
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: this.getRandomColor(),
        fillOpacity: 0.35
      }));
    }


    /*
    google.maps.event.addListener(self.drawingManager, 'polygoncomplete', function (event) {
      console.log("zove Poligon komplit!");

    });*/

    //  google.maps.event.addListener(this.polygons, "dragend", this.getPolygonCoords());

    //   google.maps.event.addListener(this.polygons.getPath(), "remove_at", this.getPolygonCoords());


    google.maps.event.addListener(self.drawingManager, 'overlaycomplete', function (event) {

      var arrayLatLng = event.overlay.getPath().getArray();
      self.nizOverley.push(event.overlay);
      self.novPoligon.push(arrayLatLng);

      console.log("zove overlej komplit!");
    });

    for (var i = 0; i < this.polygons.length; i++) {
      this.polygons[i].setMap(self.map);
    }

    /* var novPom = this.polygons.getPath().getArray();
     for (var i = 0; i < novPom.length; i++) {
       google.maps.event.addListener(novPom[i], "insert_at", self.getPolygonCoords(i));
       google.maps.event.addListener(novPom[i], "set_at", self.getPolygonCoords(i));
     }*/
    setTimeout(() => {
      google.maps.event.trigger(self.map, 'resize');
    }, 100);
  }
  /*
    getPolygonCoords(j) {
  
      console.log("PRE ");
      console.log(this.nizPoligona);
  
      this.nizPoligona[j] = this.polygons.getPath().getAt(j);
      console.log("POSLE");
      console.log(this.nizPoligona);
  
    }*/


  prikaziMapu(value) {
    var coords;

    this.http.get(`/plantations/userPlantation/${value}`)
      .map(res => res.json())
      .subscribe(data => {

        coords = data;
        this.initMap(coords);

      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  ngOnInit() {
    this.permService.getPerms().subscribe(data => {
      this.dozvole = data;
    },
      err => console.log(err),
      () => console.log('Completed'));

    this.idPlant = this.localStorageService.get(`idPlantaze`);
    this.http.get(`sessions/LogovaniKorisnik`)
      .map(res => res.json())
      .subscribe(data => {
        this.user = data;
        this.uzmiLang();
        this.uzmiBoje();
        console.log("init " + data);
        if (this.user.success === undefined) {
          this.vidljivost = true;
          this.getArrays();
          this.userIsOwner();
          this.prikaziMapu(this.idPlant);
          this.getPlantationData(this.idPlant);
        }
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  nazad() {
    this.router.navigate(['/user']);
  }
  promeniPodatke() {
    var aloo = [];
    console.log("PROIMENI!");
    for (var i = 0; i < this.polygons.length; i++) {
      aloo.push(this.polygons[i].getPath().getArray());
    }

    for (var i = 0; i < this.novPoligon.length; i++) {
      aloo.push(this.novPoligon[i]);
    }
    /*
    if (this.obrisiPol) {
      aloo = [];
      this.obrisiPol = false;
    }*/
    var novo: any;
    var proba = JSON.stringify(aloo);

    console.log(proba);
    this.http.get(`users/promeniPlantaze/${this.idPlant}/${this.user.id}/${this.imePlant}/${this.nazivTip}/${this.imePodT}/${this.imePS}/${proba}`)
      .map(res => res.json())
      .subscribe(data => {
        novo = data;
        if (novo.success) {
          this.uspesnoPromenjeniPodaci = true;

          setTimeout(() => {
            this.uspesnoPromenjeniPodaci = false;
          }, 3000);

        }

      },
      err => console.log(err),
      () => console.log('Completed'));
  }
  getArrays() {
    console.log("get arrays");
    this.isLoading = true;
    this.sviPodtipovi = this.getSubtypesById();
  }
  getSubtypesById(): any {
    this.http.get(`types/getSubtypes`)
      .map(res => res.json())
      .subscribe(data => {
        this.sviPodtipovi = data;
        console.log(" pa podtipovi" + this.sviPodtipovi);
      },
      err => console.log(err),
      () => console.log('Completed'));
    return this.sviPodtipovi;
  }

  getTypes(id): Vrsta[] {
    console.log(this.user.id + " get tajps");
    this.http.get(`types/getTypes/${id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.tipovi = data as Vrsta[];
        console.log(data + " wa wa wa");
      },
      err => console.log(err),
      () => console.log('Completed'));

    return this.tipovi;
  }


  getProducers(id): Proizvodjac[] {
    this.http.get(`types/getProducers/${id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.proizvodjaci = data as Proizvodjac[];
      },
      err => console.log(err),
      () => console.log('Completed'));
    return this.proizvodjaci;
  }
  onSelect(typeID) {
    console.log(this.sviPodtipovi[0].idType);
    this.podtipovi = this.sviPodtipovi.filter(x => x.idType == typeID);
    console.log("asd dda" + this.podtipovi);
    if (this.podtipovi.length > 0)
      this.ddVisible = true;
    else
      this.ddVisible = false;
  }

  createForm() {
    this.plantForm = this.fb.group({
      name: ['', Validators.required],
      vrsta: ['', Validators.required],
      podvrsta: [''],
      proizvodjacSemena: [''],
      vlasnikPlantaze: ['']
    });
  }

  onSubmit() {
    const model = this.plantForm.value;

    if (this.nizPoligona[0] == null && this.novPoligon[0] == null) {
      this.nemaPoligona = true;
      setTimeout(() => {
        this.nemaPoligona = false;
      }, 3000);

    }
    else {
      if (this.plantForm.value.name == null || this.plantForm.value.name == "") {
        this.imePrazno = true;
      setTimeout(() => {
        this.imePrazno = false;
      }, 3000);
      }
    
      /*  else if (!this.isOwner && (model.vlasnikPlantaze == null || model.vlasnikPlantaze == '')) {
          alert("unesite ime vlasnika plantaze" + this.plantForm.value.vlasnikPlantaze);
        }*/
      else {

        this.imePlant = this.plantForm.value.name;
        this.nazivTip = model.vrsta;
        this.imePodT = model.podvrsta;
        this.imePS = model.proizvodjacSemena;

        // this.imePlant,this.nazivTip,this.imePodT,this.imePS
        this.promeniPodatke();
      }
    }
  };

  userIsOwner() {
    this.http.get(`permisions/userIsOwner`)
      .map(res => res.json())
      .subscribe(data => {
        this.isOwner = data.owner;

        this.getOwners();

      },
      err => console.log(err),
      () => console.log('Completed'));
  }
  getOwners() {
    this.http.get(`users/getOwners`)
      .map(res => res.json())
      .subscribe(data => {
        this.sviVlasnici = data;
        this.getPerms();
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  getPerms() {
    this.http.get(`permisions/dozvoleKodVlasnika`)
      .map(res => res.json())
      .subscribe(data => {
        console.log(data.success + " " + data.dozvole)
        this.perm = data;
        if (data.dozvole !== undefined && data.dozvole.length > 0) {
          console.log()
          this.dozvoleKodGazde = data.dozvole.filter(x => x.vrednosti.plantCRUD == true);

          if ((this.dozvoleKodGazde !== undefined && this.dozvoleKodGazde.length > 0) || this.isOwner) {
            //console.log("broj sa dozvolom kod gazde " + this.dozvoleKodGazde[0].vlasnik);
            this.spoji();
            this.smeDaPise = true;
          }
        }
        else if (this.isOwner) {
          this.spoji();
          this.smeDaPise = true;
        }
      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  spoji() {
    console.log(this.sviVlasnici.length + " broj svih vlasnika");
    for (var i = 0; this.sviVlasnici.length !== undefined && i < this.sviVlasnici.length; i++)
      for (var j = 0; this.dozvoleKodGazde !== undefined && j < this.dozvoleKodGazde.length; j++) {
        console.log(this.sviVlasnici[i].idOwner + " " + this.dozvoleKodGazde[j].vlasnik);
        if (this.sviVlasnici[i].idOwner == this.dozvoleKodGazde[j].vlasnik) {

          var noviG = {
            id: this.sviVlasnici[i].idOwner,
            name: this.sviVlasnici[i].name,
            surname: this.sviVlasnici[i].surname,
            username: this.sviVlasnici[i].username,
            email: this.sviVlasnici[i].email
          };
          this.komeMozeDaDoda.push(noviG);
          this.idVlasnika.push(this.sviVlasnici[i].idOwner);
        }
      }
    if (this.isOwner) {
      var noviG = {
        id: this.user.id,
        name: this.user.name,
        surname: this.user.surname,
        username: this.user.username,
        email: this.user.email
      };
      this.komeMozeDaDoda.unshift(noviG);
      this.idVlasnika.unshift(this.user.id);
    }
    this.tipovi = this.getTypes(this.idVlasnika);
    this.proizvodjaci = this.getProducers(this.idVlasnika);

  }

  getPlantationData(id) {
    console.log("uzmi podatke o plantazi");
    this.http.get(`users/podaciOPlantazi/${id}`)
      .map(res => res.json())
      .subscribe(data => {
        console.log(data.success + " i data plant " + data.plant);
        if (data.success) {
          this.podaciOPlant = data.plant;
          console.log(this.podaciOPlant);
          this.imePlantaze = this.podaciOPlant.name;
          this.typeId = this.podaciOPlant.idType;
          this.podtipovi = this.sviPodtipovi.filter(x => x.idType == this.typeId);
          this.subtypeId = this.podaciOPlant.idSubType;
          this.producerId = this.podaciOPlant.idProducer;
          this.plantForm.controls['name'].setValue(this.imePlantaze);
          this.plantForm.controls['vrsta'].setValue(this.typeId);
          this.plantForm.controls['podvrsta'].setValue(this.subtypeId);
          this.plantForm.controls['proizvodjacSemena'].setValue(this.producerId);


          /*  this.plantForm = this.fb.group({
      name: ['', Validators.required],
      vrsta: ['', Validators.required],
      podvrsta: [''],
      proizvodjacSemena: [''],
      vlasnikPlantaze: ['']
    });*/
        }
      },
      err => console.log(err),
      () => console.log('Completed'));
  }


  color: any;
  newestColor: any;
  cssStyle: any;
  cssStyleH: any;
  cssStyleT: any;
  cssStyleTab: any;
  newCss = false;
  lang = "srb";
  selectedLangInOpt: any;
  selectedLang: any;
  uzmiBoje() {

    this.http.get(`users/getUserColor/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.color = data;
        console.log(this.color);
        if (this.color[0].colorOne == "default")
          this.cssStyle = "";
        else {
          this.cssStyle = this.color[0].colorOne;
          this.cssStyleH = this.color[0].colorOne + "Header";
          this.cssStyleT = this.color[0].colorOne + "Tab";


        }


      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  uzmiLang() {
    console.log(this.lang);
    this.http.get(`users/getUserLang/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.lang = data[0]["lang"];
        console.log(this.lang);

      },
      err => console.log(err),
      () => console.log('Completed'));

  }

}



