    postaviZaBrisanje(value) {
        this.radnikZaBrisanje = value;
    }

    filter1() {
        if (this.username !== "") {
            this.filteredList = this.useri.filter(function (el) {
                return el.toLowerCase().indexOf(this.username.toLowerCase()) > -1;
            }.bind(this));
        } else {
            this.filteredList = [];
        }
    }

    select(item) {
        this.username = item;
        this.filteredList = [];
    }

    handleClick(event) {
        var clickedComponent = event.target;
        var inside = false;
        do {
            if (clickedComponent === this.elementRef.nativeElement) {
                inside = true;
            }
            clickedComponent = clickedComponent.parentNode;
        } while (clickedComponent);
        if (!inside) {
            this.filteredList = [];
        }
    }

    onChangeRoleUpdate(roleID, workerID) {
        this.updateRole = roleID;
        this.updatedRole = true;
        setTimeout(() => {
            this.updatedRole = false;
        }, 3000);

        this.workerID = workerID;
        this.update();
        console.log(this.updateRole);

    }

