    delete() {
        var id = this.radnikZaBrisanje;

        this.http.get(`users/deleteWorkers/${id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.dataa = data
                this.getUsers();
                this.uspesnoIzbrisanRadnik = true;

            },
            err => console.log(err),
            () => console.log('Completed'));
        this.getUsersWorkers();
    }
    update() {


        this.http.get(`users/updateWorkers/${this.workerID}/${this.updateRole}`)
            .map(res => res.json())
            .subscribe(data => {
                this.dataa = data
                this.getUsersWorkers();


            },
            err => console.log(err),
            () => console.log('Completed'));


    }
    isOWnerCheck() {
        this.j = 0;
        this.http.get(`permisions/userIsOwner`)
            .map(res => res.json())
            .subscribe(data => {
                this.isOwner = data.owner
                if (this.isOwner) this.Vlasnici.push(this.user);
                console.log("@#@#");
                console.log(this.Vlasnici);
                this.http.get(`permisions/dozvoleKodVlasnika`)
                    .map(res => res.json())
                    .subscribe(data => {
                        this.dozvoleKodVlasnika = data;
                        console.log(this.dozvoleKodVlasnika);
                        for (this.j = 0; this.dozvoleKodVlasnika !== undefined && this.dozvoleKodVlasnika['dozvole'] !== undefined && this.j < this.dozvoleKodVlasnika['dozvole'].length; this.j++) {
                            console.log(this.dozvoleKodVlasnika['dozvole'][this.j].vrednosti.radnikDodajBrisi);
                            if (this.dozvoleKodVlasnika['dozvole'][this.j].vrednosti.radnikDodajBrisi) {
                                this.brojacDozvolaZaDodavanje++;
                            }
                            if (this.dozvoleKodVlasnika['dozvole'][this.j].vrednosti.radnikAzuriraj) {
                                this.brojacDozvolaZaAzuriranje++;
                                console.log(this.brojacDozvolaZaAzuriranje);
                            }
                            if (this.user && this.dozvoleKodVlasnika !== undefined && this.dozvoleKodVlasnika.dozvole !== undefined) var daLiImaDozvolu = this.dozvoleKodVlasnika.dozvole.find(x => x.vlasnik == this.dozvoleKodVlasnika['dozvole'][this.j].vlasnik && x.radnik == this.user.id && x.vrednosti.radnikDodajBrisi);


                            if (daLiImaDozvolu !== null && daLiImaDozvolu !== undefined) this.Vlasnici.push(this.data.find(x => x.id == this.dozvoleKodVlasnika['dozvole'][this.j].vlasnik));



                            console.log(this.Vlasnici);

                        }
                        this.getRoles();

                        //console.log(this.dozvoleKodVlasnika);
                        //console.log(this.dozvoleKodVlasnika['dozvole'].length);
                        if (this.Vlasnici[0] != undefined) {
                            this.selectedOwner = this.Vlasnici[0].id;
                            this.getAutoCompleteData();
                        }

                    },
                    err => console.log(err),
                    () => console.log('Completed'));

                console.log(this.isOwner);
            },
            err => console.log(err),
            () => console.log('Completed'));


    }
    uzmiBoje() {

        this.http.get(`users/getUserColor/${this.user.id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.color = data;
                console.log(this.color);
                if (this.color[0].colorOne == "default")
                    this.cssStyle = "";
                else {
                    this.cssStyle = this.color[0].colorOne;
                    this.cssStyleH = this.color[0].colorOne + "Header";
                    this.cssStyleT = this.color[0].colorOne + "Tab";


                }


            },
            err => console.log(err),
            () => console.log('Completed'));

    }

    uzmiLang() {

        this.http.get(`users/getUserLang/${this.user.id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.lang = data;
                this.selectedLang = this.meniMultiLang[this.lang[0]["lang"]];



            },
            err => console.log(err),
            () => console.log('Completed'));

    }
