import { Component, OnInit } from '@angular/core';
import { MeniComponent } from "../meni/meni.component";
import { LocalStorageService } from 'angular-2-local-storage';
import { Headers, Http } from '@angular/http';
import { PermService } from "../services/perm-service.service";
import { RouterModule, Routes, Router } from '@angular/router';

@Component({
  selector: 'app-timeline-component',
  templateUrl: './timeline.html',

})
export class Timeline implements OnInit {
  plantId: any;
  selectedLang;
  cssStyle: any;
  cssStyleH: any;
  cssStyleT: any;
  cssStyleTab: any;
  color: any;
  newestColor: any;

  lang = "srb";
  dozvole: any;
  isOwner: boolean = false;
  user: any;
  podaci: any;
  constructor(private localStorageService: LocalStorageService, private http: Http, private permService: PermService, private router: Router) { }

  ngOnInit() {
    this.plantId = this.localStorageService.get("plantazaZaTimeline");
    /*if (this.plantId == undefined) {
      this.plantId = 398;

    }*/






    this.permService.getPerms().subscribe(data => {
      this.dozvole = data;
      if (this.dozvole.guest != undefined) this.localStorageService.set("guest", this.dozvole.guest);
      if (this.dozvole.guest == true) {
        /*  $.getJSON('//freegeoip.net/json/?callback=?', function(data) {
         this.lokacija=data;
         console.log(  this.lokacija["latitude"]);
         
       });*/


      }
      this.http.get(`permisions/userIsOwner`)
        .map(res => res.json())
        .subscribe(data => {
          this.isOwner = data.owner;
        },
        err => console.log(err),
        () => console.log('Completed'));
      this.http.get(`sessions/LogovaniKorisnik`)
        .map(res => res.json())
        .subscribe(data => {
          this.user = data;
          this.uzmiBoje();
          this.uzmiLang();


        },
        err => console.log(err),
        () => console.log('Completed'));

      this.http.get(`stats/dajPlantNotif/${this.plantId}`)
        .map(res => res.json())
        .subscribe(data => {
          console.log("!");
          console.log(data);
          if (data !== false) {
            this.podaci = data;
            if (this.podaci.length > 0)
              this.setAllSeen();
          }


        },
        err => console.log(err),
        () => console.log('Completed'));



    });




  }

  setAllSeen() {
    var oznake = [];
    for (var i = 0; i < this.podaci.length; i++) {
      oznake.push(this.podaci[i].id);
    }
    this.notificationSeen(oznake);
  }

  notificationSeen(id) {
    this.http.get(`users/expNotificationsSeen/${id}`)
      .map(res => res.json())
      .subscribe(data => {
        if (data.success) {
          console.log("podesio seen na 1");
        }

      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  nazad() {
    this.router.navigate(['/user']);
  }

  uzmiBoje() {

    this.http.get(`users/getUserColor/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.color = data;
        console.log(this.color);
        if (this.color[0].colorOne == "default")
          this.cssStyle = "";
        else {
          this.cssStyle = this.color[0].colorOne;
          this.cssStyleH = this.color[0].colorOne + "Header";
          this.cssStyleT = this.color[0].colorOne + "Tab";


        }


      },
      err => console.log(err),
      () => console.log('Completed'));

  }


  uzmiLang() {

    this.http.get(`users/getUserLang/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.lang = data[0]["lang"];


      },
      err => console.log(err),
      () => console.log('Completed'));

  }

}



