import { Component, OnInit } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { LocalStorageService } from 'angular-2-local-storage';
import { RouterModule, Routes, Router } from '@angular/router';
import { } from "../meni/meni.component";
import { StatsWidgets } from '../statsWidgets/stats-widgets';

@Component({
  selector: 'app-request-ownership-page',
  templateUrl: './request.html',
  styleUrls: ['./request-ownership-page.component.css']
})
export class RequestOwnershipPageComponent implements OnInit {
  user: any;
  vidljivost = false;
  poslatZahtev = false;
  postojiUBazi = false;
  isOwner = false;
  constructor(private http: Http, private localStorageService: LocalStorageService, private router: Router) { }



  ngOnInit() {

    this.http.get(`sessions/LogovaniKorisnik`)
      .map(res => res.json())
      .subscribe(data => {
        this.http.get(`permisions/userIsOwner`)
          .map(res => res.json())
          .subscribe(data => {
            this.isOwner = data.owner;
          },
          err => console.log(err),
          () => console.log('Completed'));
        this.user = data;
        if (this.user.success === undefined) {
          this.vidljivost = true;
        }

      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  proveriZahtev() {
    var pom1: any;
    this.http.get(`users/proveriZahtevv/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        pom1 = data;
        if (pom1.success) {
          this.postojiUBazi = true;

        }

      },
      err => console.log(err),
      () => console.log('Completed'));


  }

  posaljiZahtev() {
    /*  this.proveriZahtev();
   window.alert(this.postojiUBazi);
      if (this.postojiUBazi == false) {
        var pom: any;
        this.http.get(`users/posaljiZahtevAdminu/${this.user.id}`)
          .map(res => res.json())
          .subscribe(data => {
            pom = data;
            if (pom.success) {
              this.poslatZahtev = true;
            }
  
          },
          err => console.log(err),
          () => console.log('Completed'));
  
      }*/

    var pom1: any;
    this.http.get(`users/proveriZahtevv/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        pom1 = data;
        if (pom1.success) {
          var pom: any;
          this.http.get(`users/posaljiZahtevAdminu/${this.user.id}`)
            .map(res => res.json())
            .subscribe(data => {
              pom = data;
              if (pom.success) {
                this.poslatZahtev = true;
                this.postojiUBazi = false;
              }
            },
            err => console.log(err),
            () => console.log('Completed'));
        }
        else {
          this.postojiUBazi = true;
          this.poslatZahtev = false;
        }
      },
      err => console.log(err),
      () => console.log('Completed'));


  }

}



