import { Component, OnInit } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { PermService } from "../services/perm-service.service";
import { RulesAdvanceComponent } from "../rules-advance/rules-advance.component";
import { RulesComponent } from "../rules/rules.component";
import { RulesDeleteComponent } from "../rules-delete/rules-delete.component";
import { StatsWidgets } from '../statsWidgets/stats-widgets';
@Component({
  selector: 'app-pravila',
  templateUrl: './pravila.component.html',
  styleUrls: ['./pravila.component.css']
})
export class PravilaComponent implements OnInit {
  cssStyle: any;
  cssStyleH: any;
  cssStyleT: any;
  cssStyleTab: any;
  rulesBasic = true;
  rulesAdvanced = false;
  rulesDelete = false;
  newCss = false;
  lang = "srb";
  color: any;
  user: any;
  dozvole: any;
  constructor(private http: Http,
    private permService: PermService) { }


  ngOnInit() {
    this.permService.getPerms().subscribe(data => {
      this.dozvole = data;
    },
      err => console.log(err),
      () => console.log('Completed'));
    let self = this;

    self.http.get(`sessions/LogovaniKorisnik`)
      .map(res => res.json())
      .subscribe(data => {
        this.user = data;
        this.uzmiBoje();
        this.uzmiLang();
      },
      err => console.log(err),
      () => console.log('Completed'));
  }
  promeniTabOsnovna() {
    this.rulesBasic = true;
    this.rulesAdvanced = this.rulesDelete = false;
  }
  promeniTabNapredna() {
    this.rulesAdvanced = true;
    this.rulesBasic = this.rulesDelete = false;
  }
    promeniTabBrisanje() {
    this.rulesDelete= true;
    this.rulesBasic = this.rulesAdvanced = false;
  }


  uzmiBoje() {

    this.http.get(`users/getUserColor/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.color = data;
        console.log(this.color);
        if (this.color[0].colorOne == "default")
          this.cssStyle = "";
        else {
          this.cssStyle = this.color[0].colorOne;
          this.cssStyleH = this.color[0].colorOne + "Header";
          this.cssStyleT = this.color[0].colorOne + "Tab";


        }


      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  uzmiLang() {
    console.log(this.lang);
    this.http.get(`users/getUserLang/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.lang = data[0]["lang"];
        console.log(this.lang);

      },
      err => console.log(err),
      () => console.log('Completed'));

  }

}



