import { NgModule } from '@angular/core';
import { AuthGuard } from './auth-guard-service';
import { AdminAuthGuard } from './admin-auth-guard-service';
import { LoginAuthGuard } from './login-auth-guard';
import { RouterModule, Routes } from '@angular/router';
import { UserPage } from "./UserPage/user-page.component";
import { LoginComponent } from "./login/login.component";
import { AddPlantation } from "./AddPlantation/addplantation-page.component";
import { AddWorker } from "./AddWorker/addworker-page.component";
import { StatisticsPage } from "./Statistics/statistics-page.component";
import { UpdateUser } from "./UpdateUser/update-user.component";
import { MapaComponent } from './mapa/mapa.component';
import { UpdatePlantation } from './UpdatePlantation/update-plantation.component';
import { AdminPageComponent } from "./admin-page/admin-page.component";
import { AddTypes } from './AddTypes/addTypes';
import { RequestOwnershipPageComponent } from "./request-ownership-page/request-ownership-page.component";
import { InboxComponent } from "./inbox/inbox.component";
import { MsgComponent } from "./msg/msg.component";
import { NewMsgComponent } from "./new-msg/new-msg.component";
import { SentComponent } from "./sent/sent.component";
import { ImageUploadComponent } from "./image-upload/image-upload.component";
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { PageNotFoundDComponent } from './page-not-found-d/page-not-found-d.component';
import { NotificationsComponent } from "./notifications/notifications.component";
import { UpdateParcelComponent } from "./update-parcel/update-parcel.component";
import { FinanceComponent } from "./finance/finance.component";
import { FaqComponent } from "./faq/faq.component";
import { HomeComponent } from "./home/home.component";
import { NotificationsReadComponent } from "./notifications-read/notifications-read.component";
import { Timeline } from "./timeline-component/timeline-component.component";
import { AddMarker } from "./add-marker/add-marker.component";

import { PravilaComponent } from "./pravila/pravila.component";
import { LoginSComponent } from './login-s/login-s.component';
import { ForecastComponent } from './forecast/forecast.component';
const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'updateuser', component: UpdateUser, canActivate: [AuthGuard] },
  { path: 'user', component: UserPage, canActivate: [AuthGuard] },
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'login', component: LoginComponent, canActivate: [LoginAuthGuard] },
  //{ path: 'statistics', component: StatisticsPage, canActivate: [AuthGuard] },
  { path: 'addworker', component: AddWorker, canActivate: [AuthGuard] },
  { path: 'addplantation', component: AddPlantation, canActivate: [AuthGuard] },
  { path: 'mapa', component: MapaComponent, canActivate: [AuthGuard] },

  { path: 'updateplantation', component: UpdatePlantation, canActivate: [AuthGuard] },
  { path: 'admin', component: AdminPageComponent, canActivate: [AdminAuthGuard] },
  { path: 'reqownership', component: RequestOwnershipPageComponent, canActivate: [AuthGuard] },
  { path: 'updateplantation', component: UpdatePlantation, canActivate: [AuthGuard] },
  { path: 'msg', component: MsgComponent, canActivate: [AuthGuard] },
  { path: 'newmsg', component: NewMsgComponent, canActivate: [AuthGuard] },
  { path: 'sent', component: SentComponent, canActivate: [AuthGuard] },
  { path: 'inbox', component: InboxComponent, canActivate: [AuthGuard] },
  { path: 'addtypes', component: AddTypes, canActivate: [AuthGuard] },
  { path: 'upload', component: ImageUploadComponent },
  { path: 'updateparcel', component: UpdateParcelComponent, canActivate: [AuthGuard] },
  { path: 'finance', component: FinanceComponent, canActivate: [AuthGuard] },
  { path: 'notifications', component: NotificationsComponent, canActivate: [AuthGuard] },
  { path: 'faq', component: FaqComponent, canActivate: [AuthGuard] },
  { path: 'markers', component: AddMarker, canActivate: [AuthGuard] },

  { path: 'timeline', component: Timeline, canActivate: [AuthGuard] },
  { path: 'pravila', component: PravilaComponent, canActivate: [AuthGuard] },
  { path: 'forecast', component: ForecastComponent, canActivate: [AuthGuard] },
  { path: 'login-s', component: LoginSComponent },
  { path: 'notificationsRead', component: NotificationsReadComponent, canActivate: [AuthGuard] },
  { path: '**', component: PageNotFoundComponent, canActivate: [AuthGuard] }
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }


