import { Component, OnInit } from '@angular/core';

import { Headers, Http } from '@angular/http';
import { LocalStorageService } from 'angular-2-local-storage';
import { PermService } from "../services/perm-service.service"
import { RouterModule, Routes, Router } from '@angular/router';
import { NgxPaginationModule } from 'ngx-pagination';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.css']
})
export class NotificationsComponent implements OnInit {
  constructor(private permService: PermService, private http: Http, private localStorageService: LocalStorageService, private router: Router) {
    console.log(this.router.url);
  }
  obavestenjaVidljiva: boolean = false;
  obavestenjaStigla: boolean = false;
  tip: string[];
  obavestenja: any;
  expObavestenja: any;
  expObavestenjaNone = false;
  obavestenjaNone = false;
  user: any;
  lang = "srb";
  ngOnInit() {
    let self = this;
    self.http.get(`sessions/LogovaniKorisnik`)
      .map(res => res.json())
      .subscribe(data => {
        this.user = data;

        this.uzmiLang();


      },
      err => console.log(err),
      () => console.log('Completed'));
    this.uzmiObavestenja();
    this.uzmiExpObavestenja();
  }
  odgovori(owner, worker, role, accepted) {

    this.http.get(`users/dodajRadnika/${owner}/${worker}/${role}/${accepted}`)
      .map(res => res.json())
      .subscribe(data => {
        this.uzmiObavestenja();

      },
      err => console.log(err),
      () => console.log('Completed'));
  }
  uzmiObavestenja() {
    this.http.get(`users/dohvatiObavestenja`)
      .map(res => res.json())
      .subscribe(data => {
        this.obavestenja = data;
        if (this.obavestenja == 0)
          this.obavestenjaNone = true;
        console.log(this.obavestenja)
        this.obavestenjaStigla = true;


      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  uzmiExpObavestenja() {
    this.http.get(`users/expNotifications`)
      .map(res => res.json())
      .subscribe(data => {
        if (data.success) {
          this.expObavestenja = data.not;
          if (this.expObavestenja.length == 0)
            this.expObavestenjaNone = true;
          console.log(this.expObavestenja)
          this.obavestenjaStigla = true;
        } else {
          this.expObavestenjaNone = true;
        }

      },
      err => console.log(err),
      () => console.log('Completed'));
  }
  obrisiObavestenje(id) {
    this.http.get(`users/obrisiObavestenje/${id}`)
      .map(res => res.json())
      .subscribe(data => {

        if (data == true) {
          this.uzmiObavestenja();
        }
      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  goToNot(obavestenjee: any) {
    var broj = Number(obavestenjee.dozvole);
    var max = 32;
    var dozvolebin = "";
    while (max > 0) {
      if ((broj & max) == 0) dozvolebin += "0";
      else dozvolebin += "1";
      max = max >> 1;

    }
    this.localStorageService.set("binary", dozvolebin);
    var izabranoObavestenje: any;
    izabranoObavestenje = obavestenjee;
    this.router.navigate(['./notificationsRead', izabranoObavestenje]);
  }

  notificationSeen(id) {
    this.http.get(`users/expNotificationsSeen/${id}`)
      .map(res => res.json())
      .subscribe(data => {
        if (data.success) {
          this.uzmiExpObavestenja();
        }

      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  uzmiLang() {
    console.log(this.lang);
    this.http.get(`users/getUserLang/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.lang = data[0]["lang"];
        console.log(this.lang);

      },
      err => console.log(err),
      () => console.log('Completed'));

  }

}



