import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';

import { Router } from '@angular/router';
import 'rxjs/add/operator/switchMap';
import { Headers, Http } from '@angular/http';

@Component({
  selector: 'app-msg',
  templateUrl: './msg.component.html',
  styleUrls: ['./msg.component.css']
})
export class MsgComponent implements OnInit {

  constructor(private http: Http, private route: ActivatedRoute, private redRouter: Router) { }

  user:any;
  comm:any = '';
  mess:any;
  obrisano:boolean = false;
  porukaPoslata:boolean = false;
  procitano:boolean = false;
  selectedMessage = []; 
  porZaBrisanje;
  senderZabrisanje;
  recieverZaBrisanje;
  ngOnInit() {
     this.http.get(`sessions/LogovaniKorisnik`)
        .map(res => res.json())
        .subscribe(data => {
          this.user = data.id;
          this.uzmiLang();
          this.uzmiBoje();
        })
         this.route.params
        .subscribe(params => {
            this.mess = params['id'];
          });
        this.getMessage(this.mess);
        
        console.log(this.mess);
        
  }
  postaviPorukuZaBrisanje(por,sender,reciever){
    this.porZaBrisanje = por;
    this.senderZabrisanje = sender;
    this.recieverZaBrisanje = reciever;

  }

print(): void {
    let printContents, popupWin;
    printContents = document.getElementById('panel-body').innerHTML;
    popupWin = window.open('', '_blank', 'top=0,left=0,height=100%,width=auto');
    popupWin.document.open();
    popupWin.document.write(`
      <html>
        <head>
          <title>Print tab</title>
          <style>
          //........Customized style.......
          </style>
        </head>
    <body onload="window.print();window.close()">${printContents}</body>
      </html>`
    );
    popupWin.document.close();
}

  getMessage(id){
    console.log(id);
    this.http.get(`messages/getMessage/${id}`)
      .map(res => res.json())
      .subscribe(data => {
        console.log(data[0].idReceiver+" msg komp poruka");
        this.selectedMessage = data;
        if(this.selectedMessage.length > 0)
          if(data[0].idReceiver==this.user)
            this.markAsRead(data[0].id);
      },
      err => console.log(err),
      () => console.log('Completed'));
  }
  brzOdgovor(username) {
    console.log(username+" "+this.comm);
    this.comm;
    this.http.get(`messages/sendMessage/${username}/${this.comm}`)
      .map(res => res.json())
      .subscribe(data => {
        if (data.success){
          this.porukaPoslata = true;
          this.comm = '';
        }
        else
          this.porukaPoslata = false;
      },
      err => console.log(err),
      () => console.log('Completed'));

  }
  obrisiPoruku() {
    var idSender = this.senderZabrisanje;
    var idReceiver = this.recieverZaBrisanje;
    var porukeZaBrisanje = this.porZaBrisanje;
    console.log(this.user+" == "+idSender+" == "+idReceiver+ " obrisi poruku");
    if(this.user == idSender && this.user==idReceiver){
       this.http.get(`messages/deleteYMessages/${porukeZaBrisanje}`)
        .map(res => res.json())
        .subscribe(data => {
          if (data.success)
            this.obrisano = true;
          else
            this.obrisano = false;
        },
        err => console.log(err),
        () => console.log('Completed'));
        this.redRouter.navigate(['/inbox']);
    }
    else if(this.user == idSender){
      this.http.get(`messages/deleteSenderMessages/${porukeZaBrisanje}`)
        .map(res => res.json())
        .subscribe(data => {
          if (data.success)
            this.obrisano = true;
          else
            this.obrisano = false;
        },
        err => console.log(err),
        () => console.log('Completed'));
        this.redRouter.navigate(['/inbox']);
    }
    else{
      this.http.get(`messages/deleteReceiverMessages/${porukeZaBrisanje}`)
        .map(res => res.json())
        .subscribe(data => {
          if (data.success)
            this.obrisano = true;
          else
            this.obrisano = false;
        },
        err => console.log(err),
        () => console.log('Completed'));
        this.redRouter.navigate(['/inbox']);
    }
  }
  markAsRead(procitanePoruke) {
    this.http.get(`messages/markAsRead/${procitanePoruke}`)
      .map(res => res.json())
      .subscribe(data => {
        if (data.success)
          this.procitano = true;
        else
          this.procitano = false;
      },
      err => console.log(err),
      () => console.log('Completed'));

  }


  color: any;
  newestColor: any;
  cssStyle: any;
  cssStyleH: any;
  cssStyleT: any;
  cssStyleTab: any;
  newCss = false;
  lang = "srb";
  selectedLangInOpt: any;
  selectedLang: any;
  uzmiBoje() {

    this.http.get(`users/getUserColor/${this.user}`)
      .map(res => res.json())
      .subscribe(data => {
        this.color = data;
        console.log(this.color);
        if (this.color[0].colorOne == "default")
          this.cssStyle = "";
        else {
          this.cssStyle = this.color[0].colorOne;
          this.cssStyleH = this.color[0].colorOne + "Header";
          this.cssStyleT = this.color[0].colorOne + "Tab";


        }


      },
      err => console.log(err),
      () => console.log('Completed'));

  }

uzmiLang() {
    console.log( this.lang);
    this.http.get(`users/getUserLang/${this.user}`)
    .map(res => res.json())
    .subscribe(data => {
           this.lang = data[0]["lang"];
        console.log( this.lang);
       
    },
    err => console.log(err),
    () => console.log('Completed'));

}

}



