export class Plantation{
    
        public name='';
        public vrsta:Vrsta;
        public podvrsta:Vrsta;
        public proizvodjacSemena:Proizvodjac;
    
}


export class Vrsta{
    public id = 0;
    public pid;
    public name = '';
}

export class Proizvodjac{
    id=0;
    name='';
}

export const tipovi: Vrsta[] =  [
         {id: 1, pid:-1, name: "jabuka"},
         {id: 2, pid:-1, name: "kruska"},
         {id: 3, pid:-1, name: "dunja"},
         {id: 4, pid:-1, name: "cvekla"}
    ];

export const podtipovi: Vrsta[] = [
        { id:1, pid:1, name:"zlatni delises"},
        {id:2, pid:1, name:"sivi delises"},
        {id:3, pid:2, name:"Socna zelena"},
    ];

export const proizvodjaci: Proizvodjac[] = [
        {id:1, name:"ns seme"},
        {id:2, name: "zemun polje"}
    ];


