import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ActivatedRoute, Params } from '@angular/router';
import { StatsWidgets } from '../statsWidgets/stats-widgets';
import { MeniComponent } from "../meni/meni.component";
import { Headers, Http } from '@angular/http';
import { LocalStorageService } from 'angular-2-local-storage';

@Component({
  selector: 'app-notifications-read',
  templateUrl: './notifications-read.component.html',
  styleUrls: ['./notifications-read.component.css']
})
export class NotificationsReadComponent implements OnInit {
  notif: any;
  imanja: any;
  binary = this.localStorageService.get("binary");
  user: any;
  lang = "srb";
  color: any;
  newestColor: any;
  cssStyle: any;
  cssStyleH: any;
  cssStyleT: any;
  cssStyleTab: any;
  newCss = false;
  constructor(private http: Http, private route: ActivatedRoute, private router: Router, private localStorageService: LocalStorageService) { }

  ngOnInit() {
    let self = this;
    self.http.get(`sessions/LogovaniKorisnik`)
      .map(res => res.json())
      .subscribe(data => {
        this.user = data;
        this.uzmiBoje();
        this.uzmiLang();


      },
      err => console.log(err),
      () => console.log('Completed'));
    this.route.params
      .subscribe(params => {
        this.notif = params;
        this.vratiImanje();
      });


  }
  obrisiObavestenje(id) {
    this.http.get(`users/obrisiObavestenje/${id}`)
      .map(res => res.json())
      .subscribe(data => {

        if (data == true) {
          this.router.navigate(['/notifications']);
        }
      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  nazad() {
    this.router.navigate(['/notifications']);
  }

  vratiImanje() {
    this.http.get(`users/prikaziImanje/${this.notif.idowner}`)
      .map(res => res.json())
      .subscribe(data => {
        this.imanja = data;
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  uzmiLang() {
    console.log(this.lang);
    this.http.get(`users/getUserLang/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.lang = data[0]["lang"];
        console.log(this.lang);

      },
      err => console.log(err),
      () => console.log('Completed'));

  }
  odgovori(accepted) {

    this.http.get(`users/dodajRadnika/${this.notif.idowner}/${this.notif.idworker}/${this.notif.idrole}/${accepted}`)
      .map(res => res.json())
      .subscribe(data => {
        this.router.navigate(['./notifications']);
      },
      err => console.log(err),
      () => console.log('Completed'));
  }
  uzmiBoje() {

    this.http.get(`users/getUserColor/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.color = data;
        console.log(this.color);
        if (this.color[0].colorOne == "default")
          this.cssStyle = "";
        else {
          this.cssStyle = this.color[0].colorOne;
          this.cssStyleH = this.color[0].colorOne + "Header";
          this.cssStyleT = this.color[0].colorOne + "Tab";


        }


      },
      err => console.log(err),
      () => console.log('Completed'));

  }

}



