import {Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import * as plant from './plantation';

@Injectable()
export class PlantationService{
    getTypes():plant.Vrsta[]{
        return plant.tipovi;
    }
    getSubTypes():plant.Vrsta[]{
        return plant.podtipovi;
    }
    getProducers():plant.Proizvodjac[]{
        return plant.proizvodjaci;
    }
    getSingleType(ID):plant.Vrsta{
        return plant.tipovi.find(x => x.id == ID);
    }
    getSingleSubtype(ID):plant.Vrsta{
        return plant.podtipovi.find(x => x.id == ID);
    }
    getSingleProducer(ID):plant.Proizvodjac{
        return plant.proizvodjaci.find(x => x.id == ID);
    }
    getSubtypesById(pid):plant.Vrsta[]{
        return plant.podtipovi.filter(x => x.pid == pid);
    }
}


