import { Component, OnInit } from '@angular/core';
import { RouterModule, Routes, Router } from '@angular/router';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { DatePickerOptions, DateModel } from 'ng2-datepicker';

import { WeatherMapComponent } from '../WeatherMap/weather-map.component'

import { LocalStorageService } from 'angular-2-local-storage';
//import {DatePicker} from "../DatePicker/datapicker-component";
@Component({
  selector: 'app-login-s',
  templateUrl: './login-s.component.html',
  styleUrls: ['./login-s.component.css']
})
export class LoginSComponent implements OnInit {

    imeNijePopunjeno: any;
    username = "";
    password = "";
    pogresniPodaci = false;
    data: any;
    country_array = new Array("Afghanistan", "Aland Islands", "Albania", "Algeria", "American Samoa", "Andorra", "Angola", "Anguilla", "Antarctica", "Antigua", "Argentina", "Armenia", "Aruba", "Australia", "Austria", "Azerbaijan", "Bahamas", "Bahrain", "Bangladesh", "Barbados", "Barbuda", "Belarus", "Belgium", "Belize", "Benin", "Bermuda", "Bhutan", "Bolivia", "Bosnia", "Botswana", "Bouvet Island", "Brazil", "British Indian Ocean Trty.", "Brunei Darussalam", "Bulgaria", "Burkina Faso", "Burundi", "Caicos Islands", "Cambodia", "Cameroon", "Canada", "Cape Verde", "Cayman Islands", "Central African Republic", "Chad", "Chile", "China", "Christmas Island", "Cocos (Keeling) Islands", "Colombia", "Comoros", "Congo", "Congo, Democratic Republic of the", "Cook Islands", "Costa Rica", "Cote d'Ivoire", "Croatia", "Cuba", "Cyprus", "Czech Republic", "Denmark", "Djibouti", "Dominica", "Dominican Republic", "Ecuador", "Egypt", "El Salvador", "Equatorial Guinea", "Eritrea", "Estonia", "Ethiopia", "Falkland Islands (Malvinas)", "Faroe Islands", "Fiji", "Finland", "France", "French Guiana", "French Polynesia", "French Southern Territories", "Futuna Islands", "Gabon", "Gambia", "Georgia", "Germany", "Ghana", "Gibraltar", "Greece", "Greenland", "Grenada", "Guadeloupe", "Guam", "Guatemala", "Guernsey", "Guinea", "Guinea-Bissau", "Guyana", "Haiti", "Heard", "Herzegovina", "Holy See", "Honduras", "Hong Kong", "Hungary", "Iceland", "India", "Indonesia", "Iran (Islamic Republic of)", "Iraq", "Ireland", "Isle of Man", "Israel", "Italy", "Jamaica", "Jan Mayen Islands", "Japan", "Jersey", "Jordan", "Kazakhstan", "Kenya", "Kiribati", "Korea", "Korea (Democratic)", "Kuwait", "Kyrgyzstan", "Lao", "Latvia", "Lebanon", "Lesotho", "Liberia", "Libyan Arab Jamahiriya", "Liechtenstein", "Lithuania", "Luxembourg", "Macao", "Macedonia", "Madagascar", "Malawi", "Malaysia", "Maldives", "Mali", "Malta", "Marshall Islands", "Martinique", "Mauritania", "Mauritius", "Mayotte", "McDonald Islands", "Mexico", "Micronesia", "Miquelon", "Moldova", "Monaco", "Mongolia", "Montenegro", "Montserrat", "Morocco", "Mozambique", "Myanmar", "Namibia", "Nauru", "Nepal", "Netherlands", "Netherlands Antilles", "Nevis", "New Caledonia", "New Zealand", "Nicaragua", "Niger", "Nigeria", "Niue", "Norfolk Island", "Northern Mariana Islands", "Norway", "Oman", "Pakistan", "Palau", "Palestinian Territory, Occupied", "Panama", "Papua New Guinea", "Paraguay", "Peru", "Philippines", "Pitcairn", "Poland", "Portugal", "Principe", "Puerto Rico", "Qatar", "Reunion", "Romania", "Russian Federation", "Rwanda", "Saint Barthelemy", "Saint Helena", "Saint Kitts", "Saint Lucia", "Saint Martin (French part)", "Saint Pierre", "Saint Vincent", "Samoa", "San Marino", "Sao Tome", "Saudi Arabia", "Senegal", "Serbia", "Seychelles", "Sierra Leone", "Singapore", "Slovakia", "Slovenia", "Solomon Islands", "Somalia", "South Africa", "South Georgia", "South Sandwich Islands", "Spain", "Sri Lanka", "Sudan", "Suriname", "Svalbard", "Swaziland", "Sweden", "Switzerland", "Syrian Arab Republic", "Taiwan", "Tajikistan", "Tanzania", "Thailand", "The Grenadines", "Timor-Leste", "Tobago", "Togo", "Tokelau", "Tonga", "Trinidad", "Tunisia", "Turkey", "Turkmenistan", "Turks Islands", "Tuvalu", "Uganda", "Ukraine", "United Arab Emirates", "United Kingdom", "United States", "Uruguay", "US Minor Outlying Islands", "Uzbekistan", "Vanuatu", "Vatican City State", "Venezuela", "Vietnam", "Virgin Islands (British)", "Virgin Islands (US)", "Wallis", "Western Sahara", "Yemen", "Zambia", "Zimbabwe");
    acitveReg = false;
    activeLogin = true;
    forgotPass = false;;
    dataa: any;
    usernameReg: any;
    passwordReg: any;
    passwordReg2: any;
    emailReg: any;
    imeReg: any;
    prezimeReg: any;
    pogresniPodaciReg = false;
    praznaPolja = false;
    uspesnaRegistracija = false;
    drzava: string;
    nijeIsto = false;
    date: DateModel;
    options: DatePickerOptions;
    calendarOptions = {
        format: "DD-MM-YYYY",

    };
    duplo = false;
    uspesno: any;
    user1: any;
    pomm: any; //email
    nePostoji = false;
    pom2: any; //random sifra
    poslato = false;

    constructor(private http: Http, private router: Router, private localStorageService: LocalStorageService) {
        this.options = new DatePickerOptions();
        this.drzava = this.country_array[0];
    }

    ngOnInit() {
        this.localStorageService.set("logged", false);
        this.username = "";
        this.password = "";
    }


    povratiSifru() {
        var pom1: any;
        this.http.get(`users/proveriUser/${this.user1}`)
            .map(res => res.json())
            .subscribe(data => {
                pom1 = data;
                if (pom1.success) {

                    this.http.get(`users/vratiEmail/${this.user1}`)
                        .map(res => res.json())
                        .subscribe(data => {
                            this.pomm = data;
                            this.http.get(`users/probaSifre`)
                                .map(res => res.json())
                                .subscribe(data => {
                                    this.pom2 = data;
                                    this.nePostoji = false;
                                    this.posaljiNaMail();
                                },
                                err => console.log(err),
                                () => console.log('Completed'));
                        },
                        err => console.log(err),
                        () => console.log('Completed reg'));
                }
                else {
                    this.nePostoji = true;
                }
            },
            err => console.log(err),
            () => console.log('Completed'));



    }

    resetuj() {
        this.pogresniPodaci = false;
        this.pogresniPodaciReg = false;
        this.praznaPolja = false;
        this.uspesnaRegistracija = false;
    }
    resetujL() {
        this.acitveReg = false;
        this.activeLogin = true;
        this.forgotPass = false;

        this.pogresniPodaci = false;
        this.pogresniPodaciReg = false;
        this.praznaPolja = false;
        this.uspesnaRegistracija = false;
    }
    ForgotPass() {
        this.acitveReg = false;
        this.activeLogin = false;
        this.forgotPass = true;

    }
    resetujR() {
        this.forgotPass = false;
        this.acitveReg = true;
        this.activeLogin = false;
        this.pogresniPodaci = false;
        this.pogresniPodaciReg = false;
        this.praznaPolja = false;
        this.uspesnaRegistracija = false;
    }


    posaljiNaMail() {
        var pom3: any;
        var email = this.pomm;
        var sifra = this.pom2;
        this.http.get(`users/posaljiMail/${email}/${sifra}`)
            .map(res => res.json())
            .subscribe(data => {
                pom3 = data;
                if (pom3.success) {
                    this.http.get(`users/promeniSifru2/${this.user1}/${sifra}`)
                        .map(res => res.json())
                        .subscribe(data => {
                            var pom4: any;
                            pom4 = data;
                            if (pom4.success) {
                                this.poslato = true;

                                this.user1 = "";
                            }
                        },
                        err => console.log(err),
                        () => console.log('Completed'));
                }
            },
            err => console.log(err),
            () => console.log('Completed'));
    }





    callType(value) { /*da dobijem naziv drzave */
        this.drzava = value;
    }

    Login() {
        var pom: any;
        //window.alert(this.username+" "+this.password);
        this.http.get(`users/users/${this.username}/${this.password}`)
            .map(res => res.json())
            .subscribe(data => {
                this.data = data;
                if (this.data.success) {
                    this.localStorageService.set("logged", true);
                    this.localStorageService.set("password", this.password);
                    this.http.get(`users/proveriAdmina/${this.username}/${this.password}`)
                        .map(res => res.json())
                        .subscribe(data => {
                            pom = data;
                            if (pom.success) {
                                this.localStorageService.set("admin", true);
                                this.router.navigate(['./admin']);
                            }
                            else {
                                this.localStorageService.set("admin", false);
                                this.router.navigate(['./user']);
                            }
                        },
                        err => console.log(err),
                        () => console.log('Completed'));
                }
                else {
                    this.pogresniPodaci = true;
                    setTimeout(() => {
                        this.pogresniPodaci = false;
                    }, 3000);
                }

            },
            err => console.log(err),
            () => console.log('Completed'));

    }



    RegistrujSe() {
        if (this.imeReg == null || this.prezimeReg == null || this.usernameReg == null || this.passwordReg == null || this.emailReg == null) {
            this.imeNijePopunjeno = true;
            this.praznaPolja = true;
            this.uspesno = false;
            setTimeout(() => {
                this.praznaPolja = false;
            }, 3000);
        }
        else if (this.passwordReg != this.passwordReg2) {
            this.nijeIsto = true;
            setTimeout(() => {
                this.nijeIsto = false;
            }, 3000);
        }
        else if (this.imeReg != null && this.prezimeReg != null && this.usernameReg != null && this.passwordReg != null && this.emailReg != null) {
            this.praznaPolja = false;

            var pod: any;

            var pod2: any;
            this.http.get(`users/proveriMail/${this.emailReg}/${this.usernameReg}`)
                .map(res => res.json())
                .subscribe(data => {
                    pod2 = data;
                    if (pod2.success == false) {
                        this.duplo = true;
                        setTimeout(() => {
                            this.duplo = false;
                        }, 3000);
                        this.http.get(`users/posaljiMail4/${this.emailReg}`)
                            .map(res => res.json())
                            .subscribe(data => {
                                var pom4: any;
                                pom4 = data;
                            }, err => console.log(err),
                            () => console.log('Completed provera'));

                    }
                    else {
                        this.http.get(`users/register/${this.usernameReg}/${this.passwordReg}`)
                            .map(res => res.json())
                            .subscribe(data => {
                                this.data = data;
                                if (this.data.success) {
                                    this.popuniBazu();
                                    this.http.get(`users/posaljiMail3/${this.emailReg}/${this.usernameReg}/${this.passwordReg}`)
                                        .map(res => res.json())
                                        .subscribe(data => {
                                            pod = data;

                                            if (pod.success == true) {
                                                this.uspesno = true;
                                                this.imeReg = "";
                                                this.prezimeReg = "";
                                                this.emailReg = "";
                                                this.usernameReg = "";
                                                this.passwordReg = "";


                                            }
                                        },
                                        err => console.log(err),
                                        () => console.log('Completed provera'));
                                }
                                else {
                                    this.pogresniPodaciReg = true;
                                    setTimeout(() => {
                                        this.pogresniPodaciReg = false;
                                    }, 3000);
                                }
                            },
                            err => console.log(err),
                            () => console.log('Completed provera'));

                    }
                },
                err => console.log(err),
                () => console.log('Completed provera'));
        }
    }





    popuniBazu() {
        console.log(this.drzava);
        this.http.get(`users/dodajUBazu/${this.usernameReg}/${this.passwordReg}/${this.emailReg}/${this.imeReg}/${this.prezimeReg}/${this.drzava}/${this.date.formatted}`)
            .map(res => res.json())
            .subscribe(data => {
                this.dataa = data
                console.log(this.dataa.success);
                if (this.dataa.success) {
                    this.uspesnaRegistracija = true;
                    this.imeReg = null;

                    setTimeout(() => {
                        this.uspesnaRegistracija = false;
                    }, 5000);
                    this.pogresniPodaciReg = false;
                }
                else {


                    this.uspesnaRegistracija = false;
                }
            },
            err => console.log(err),
            () => console.log('Completed reg'));

    }

}


